a {
	text-decoration: none; }

header {
	position: relative;
	height: 50px; }

nav {
	background: #212121;
	height: 50px; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	&__link {
		color: #FFFFFF;
		font-weight: 300;
		font-size: 15px; }

	&__item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }

.hamburger {
	display: none; }

.subheader {
	position: absolute;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.6);
	height: 84px;

	&__logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	&__official {
		margin-top: 6px;
		font-weight: 400;
		font-size: 13px;
		color: #FFFFFF;
		opacity: 0.5; }

	&__call {
		color: #FFFFFF;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	&__phone {
		display: block;
		margin-top: 8px;
		color: #FFFFFF;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }

	&__btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		color: #FFFFFF;
		font-weight: 400;
		font-size: 14px;
		@include btn_hover; } }



.promo {
	min-height: 800px;
	background: url(../img/uber_bg.png) center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	&__header {
		color: #FFFFFF;
		font-size: 60px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 50px;
		margin: 0;
		text-align: center; }

	&__subheader {
		color: #FFFFFF;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }

	&__descr {
		color: #FFFFFF;
		font-weight: 400;
		font-size: 18px;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	&__btn {
		display: block;
		margin: 118px auto 0;
		border: none;
		border-radius: 4px;
		width: 211px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		font-weight: 400;
		font-size: 18px;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		@include btn_hover; } }


.reasons {
	padding: 71px 0 65px 0; }

.label {
	margin: 0 auto;
	margin-top: 50px;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	&_1 {
		position: relative;
		background-color: #ffffff;
		color: #000000;
		z-index: 2; } }


.subtitle {
	margin-top: 19px;
	font-weight: 400;
	font-size: 17px;
	color: #222222;
	text-align: center;
	&_st1 {
		position: relative;
		color: #ffff;
		margin-top: 5px;
		z-index: 3; } }

.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	&_t1 {
		position: relative;
		color: #ffff;
		z-index: 3; } }

.second__section {
	.reasons__block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }

	.second__section-img {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background: #1EACC7;
		border-radius: 100%; }

	.second__section-text {
		width: 343px;
		font-weight: 300;
		font-size: 15px;
		line-height: 21px;
		color: #202020; }

	span {
		font-weight: 400;
		font-size: 17px;
		line-height: 21px;
		color: #1EACC7; }

	.second__section-img-logo {
		margin: auto; } }

.mobile {
	min-height: 865px;
	padding: 48px 0 79px 0;
	background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/cart/1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/cart/2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/cart/3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/cart/4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/cart/5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/cart/6.png') center (center / cover) no-repeat; }
		&_subtitle {
			font-weight: 400;
			font-size: 24px;
			line-height: 31px;
			text-transform: uppercase;
			color: #FFFFFF;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 29px;
			height: 29px;
			background: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $main-color;
				transition: 0.3s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				height: 14px;
				width: 2px;
				background-color: $main-color;
				transition: 0.3s all; }
			&:hover {
				&:before, &:after {
					background-color: $second-color; } } } } }

.car {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/bg_car.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2;
		text-align: center; }
	&_black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

.require {
	padding: 68px 0 84px 0;
	&_block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&_title {
		font-weight: 400;
		font-size: 22px;
		line-height: 24px;
		color: #1CA8C3; }
	&_list {
		list-style-type: none;
		padding-left: 50px;
		margin-top: 22px;
		li {
			position: relative;
			font-weight: 300;
			font-size: 17px;
			line-height: 24px;
			color: #222222;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				width: 21px;
				height: 20px;
				background: url('../icons/check_blue_1.svg') center (center / cover) no-repeat; } } }
	&_descr {
		margin-top: 25px;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px;
		color: #1ca8c3; }
	.warning {
		.require_title {
			color: $second-color; }
		.require_list {
			li {
				&:before {
					background: url('../icons/check_red_1.svg') center (center / cover) no-repeat; } } } } }

.world {
	background: url('../img/world_bg.png') center (center / cover) no-repeat;
	min-height: 716px;
	padding: 50px 0 50px 0;
	position: relative;
	&_map {
		&_link {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 49px;
			text-align: center;
			font-size: 15px;
			font-weight: 400;
			line-height: 68px;
			text-transform: uppercase;
			color: #1fbad6;
			&:hover {
				text-decoration: none;
				color: #fff; } } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }

.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			span {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } }
	&_mobile {} }

.xs-visible {
	display: none; }
